  background-attachment: fixed;
  height: 400px;
  position: relative;
  overflow: hidden;
}

.parallax-section .parallax-content {
  text-align: center;
  color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.parallax-section .parallax-content h2 {
  margin-bottom: 20px;
